*{
  background-color: #0f222c;
  /* background-color: rgb(197, 186, 186); */
}

.ttt{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100vh;
}

.title{
  margin-top: 50px;
  color: white;
  font-size: 60px;
}


.board-row{
  display: flex;
  justify-content: center;
  align-items: center;
}

.square{
  background-color: #1f3540;
  height: 100px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  margin: 3px;
}

.square:hover{
  box-shadow: 1px 1px 10px  black;
}

.square h1{
  font-size: 5rem;
  font-weight: 400;
  background-color: #1f3540;
  color: aquamarine;
}

.move{
  color: white;
  display: block;
  text-align: center;
}

.won{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.title1{
  color: white;
  font-size: 60px;
}

.reset{
  margin-top: 20px;
  background-color:#1f3540;
  height: 80px;
  width: 150px;
  color: white;
  font-size: 20px;
  font-weight: 700;
  border-radius: 20px;
  padding: 10px;
  border: 1px #0f222c;
  box-shadow: 1px 1px 10px  black;
}

.reset:hover{
  background-color: rgb(99, 158, 139);
  color: black;
  font-weight: 700;
  box-shadow: 1px 1px 10px  black;
}